	z-index: 2
}

html.mm-dropdown.mm-opened:not(.mm-opening) .mm-menu {
	display: none
}

.mm-menu.mm-tip:before {
	content: '';
	background: inherit;
	box-shadow: 0 2px 10px rgba(0, 0, 0, .3);
	display: block;
	width: 15px;
	height: 15px;
	position: absolute;
	z-index: 0;
	-webkit-transform: rotate(45deg);
